import React from "react";

import {
  RDContainer,
  TextContainer,
  TitleContainer,
  Title,
  Paragraph,
  AnchorButton,
  ButtonStyle,
  Span
} from "./RestaurantDescription.styled";

const RestaurantDescription = () => (
  <RDContainer>
    <TextContainer>
      <TitleContainer>
        <Title>{`Velkomin`}</Title>
        <Title>{`  Tryggvaskla`}</Title>
      </TitleContainer>
      <Paragraph>
        Tryggvaskli er einstaklega fallegur ala carte veitingastaur sem leggur herslu  vandaa
        matreislu me fkus  hrefni r hrai. Me viringu fyrir strfum bnda, tba
        matreislumenn staarins virkilega vandaan mat ar sem slenskar- og erlendar
        matreisluaferir blandast skemmtilega saman.
      </Paragraph>
      <AnchorButton href='https://goo.gl/maps/i8qw8M8nZZrnCPNA9'>
        <ButtonStyle> Stasetning </ButtonStyle>
        <Span> {">"} </Span>
      </AnchorButton>
    </TextContainer>
  </RDContainer>
);

export default RestaurantDescription;
