import React from "react";
import styled from "styled-components";
import Layout from "@Components/Layout";
import { Colors } from "@Assets";

const Menu = () => {
  return (
    <Layout bgColor={Colors.GREEN}>
      <MContainer></MContainer>
    </Layout>
  );
};

export default Menu;

const MContainer = styled.div`
  height: 100%;
  width: 100%;
  position: relative;
`;
